import {
  BaseRenderTexture,
  Container,
  Graphics,
  RenderTexture,
  SCALE_MODES,
  Sprite,
  Texture,
  filters,
} from "pixi.js";
import { mat4, vec3 } from "gl-matrix";

import AbstractScene from "./helpers/AbstractScene";
import Background from "./Background";
import { BloomFilter } from "@pixi/filter-bloom";
import Camera from "./cameras/CameraPerspective";
import LCD from "./LCD";
import OrbitalControl from "./helpers/OrbitalControl";
import PixiPlane from "pixi-plane";
import PointPlane from "./PointPlane";
import Reflection from "./Reflection";
import { app } from "./WebGLAnimation";

const vec3temp = vec3.create();
const mat4temp = mat4.create();

const r = 1920 / 1080;
const w = 300;
const h = (300 * 1) / r;

const videos = ["video1.mp4", "video2.mp4", "video3.mp4"];
export default class Scene extends AbstractScene {
  constructor() {
    super();

    this.indexVideo = Math.floor(Math.random() * videos.length);

    this.tick = 0;

    this.targetCamera = {
      x: 0,
      y: 0,
      rx: -0.0954,
      ry: 0.22036728296473396,
    };

    // low def stuff
    const brtLowdef = new BaseRenderTexture(w, h, SCALE_MODES.NEAREST, 0.01);
    this.lowDefTexture = new RenderTexture(brtLowdef);

    this.spriteTest = new Sprite(this.lowDefTexture);
    this.spriteTest.width = w;
    this.spriteTest.height = h;

    this.spriteLowdef = new Sprite();
    this.spriteLowdef.width = w;
    this.spriteLowdef.height = h;

    this.camera = new Camera();
    this.orbitalControl = new OrbitalControl(this.camera, app.view, 300);
    this.orbitalControl.lock(true);
    this.orbitalControl._rx.value = this.targetCamera.rx;
    this.orbitalControl._ry.value = this.targetCamera.ry;

    this.containerScene = new Container();

    this.tvPlane = new PixiPlane(Texture.WHITE);
    this.floorPlane = new PixiPlane(Texture.WHITE);
    this.background = new Background();

    this.containerScene.addChild(this.background.view);

    this.containerFloor = new Container();
    this.containerFloor.filterArea = app.screen;
    this.containerFloor.addChild(this.floorPlane);
    this.floorPlane.setAlpha(0.5);

    this.containerScene.addChild(this.containerFloor, this.tvPlane);
    this.view.addChild(this.containerScene);
    this.reflection = new Reflection();
    this.lcd = new LCD();

    this.containerFloor.filters = [new filters.BlurFilter(4, 2)];

    this.corners = [];
    this.ptsContainer = new Container();

    this.view.interactive = true;
    this.view.mousemove = this.onMove.bind(this);
    this.view.touchmove = this.onMove.bind(this);

    // base texture for the floor
    const screenBRT = new BaseRenderTexture(w, h, SCALE_MODES.LINEAR, 2);
    this.screenRT = new RenderTexture(screenBRT);

    const brt = new BaseRenderTexture(w, h, SCALE_MODES.LINEAR, 0.5);
    this.floorRT = new RenderTexture(brt);

    this.ptsScreen = [
      vec3.fromValues(-w / 2, -h / 2, 0),
      vec3.fromValues(w / 2, -h / 2, 0),
      vec3.fromValues(w / 2, h / 2, 0),
      vec3.fromValues(-w / 2, h / 2, 0),
    ];

    this.ptsFloor = [
      vec3.fromValues(-w / 2, h / 2 - 2, h),
      vec3.fromValues(w / 2, h / 2 - 2, h),
      vec3.fromValues(w / 2, h / 2 - 2, 0),
      vec3.fromValues(-w / 2, h / 2 - 2, 0),
    ];

    this.view.filterArea = app.screen;

    this.g = new Graphics();
    this.containerScene.addChild(this.g);
  }

  onMove(e) {
    let { x, y } = e.data.getLocalPosition(this.view);
    x = x / window.innerWidth;
    x -= 0.5;
    x *= 2;

    y = y / window.innerHeight;
    y -= 0.5;
    y *= 2;

    this.targetCamera.x = x * Math.min(window.innerWidth / 10, 70);
    this.targetCamera.y = y * Math.min(window.innerHeight / 10, 70);

    this.orbitalControl._rx.value = this.targetCamera.rx - y * 0.06;
    this.orbitalControl._ry.value = this.targetCamera.ry + x * 0.06;
  }

  getVideoTexture() {
    const textureVideo = Texture.from(
      `assets/video/${videos[this.indexVideo]}`
    );
    textureVideo.baseTexture.resource.source.muted = true;
    textureVideo.baseTexture.resource.source.loop = false;
    textureVideo.baseTexture.resource.source.onended = this.onEnded.bind(this);

    this.indexVideo++;
    this.indexVideo = this.indexVideo % videos.length;

    return textureVideo;
  }

  onEnded() {
    const textureVideo = this.getVideoTexture();
    textureVideo.baseTexture.resource.source.play();
    this.spriteLowdef.texture = textureVideo;
    this.lcd.texture = textureVideo;
    this.reflection.texture = textureVideo;

    this.reflection.resize(w, h);
    this.lcd.resize(w, h);
  }
  onShow() {
    const textureVideo = this.getVideoTexture();

    this.spriteLowdef.texture = textureVideo;
    this.lcd.texture = textureVideo;
    this.reflection.texture = textureVideo;

    this.tvPlane.texture = this.screenRT;
    this.floorPlane.texture = this.floorRT;

    this.reflection.resize(w, h);
    this.lcd.resize(w, h);
  }

  getPixels() {
    const pixels = app.renderer.plugins.extract.pixels(this.lowDefTexture);

    let r = 0;
    let g = 0;
    let b = 0;
    let count = 0;
    for (let i = 0; i < pixels.length; i += 4) {
      r += pixels[i] / 255;
      g += pixels[i + 1] / 255;
      b += pixels[i + 2] / 255;

      if (pixels[i + 4] > 0) {
        count++;
      }
    }

    this.background.color = [r / count, g / count, b / count];
  }
  logPoints() {
    this.tvPlane.points.forEach((pt, i) => {
      console.log(i, `{ x: ${pt.x}, y: ${pt.y}}`);
    });
  }

  resizePlane() {
    const paddingTop = 40;
    const { width: vW, height: vH } = app.screen;
    const { width, height } = this.tvPlane.texture.baseTexture;

    const scale = Math.min(1, vW / width, vH - paddingTop, height);

    const pts = this.tvPlane.points;

    pts[0].y = paddingTop;
    pts[1].x = width * scale;
    pts[1].y = paddingTop;
    pts[2].x = width * scale;
    pts[2].y = height * scale + paddingTop;
    pts[3].y = height * scale + paddingTop;
    this.tvPlane.update();
  }

  update() {
    this.tick++;

    const c = this.orbitalControl.center;
    c[0] += (this.targetCamera.x - c[0]) * 0.03;
    c[1] += (this.targetCamera.y - c[1]) * 0.03;

    if (this.tick % 4 === 0) {
      this.getPixels();
    }
    this.background.update();
    this.g.clear();
    this.g.lineStyle(2, 0xbbbbbb, 0.15);

    const pts = this.tvPlane.points;

    app.renderer.render(this.reflection.view, this.floorRT);
    app.renderer.render(this.lcd.view, this.screenRT);
    app.renderer.render(this.spriteLowdef, this.lowDefTexture);

    this.ptsScreen.forEach((pt, i) => {
      const currPoint = pts[i];
      vec3.transformMat4(vec3temp, pt, this.camera._matrix);
      const x = vec3temp[0];
      const y = vec3temp[1];
      const z = vec3temp[2];

      const xC = this.camera.position[0];
      const yC = this.camera.position[1];
      const zC = this.camera.position[2];

      const f = z - zC;

      const x2 = (x - xC) * (f / z) + xC;
      const y2 = (y - yC) * (f / z) + yC;

      currPoint.x = x2;
      currPoint.y = y2;
    });

    this.g.moveTo(pts[3].x, pts[3].y);
    this.g.lineTo(pts[0].x, pts[0].y);
    this.g.lineTo(pts[1].x, pts[1].y);
    this.g.lineTo(pts[2].x, pts[2].y);

    this.tvPlane.update();

    const ptsFloor = this.floorPlane.points;
    this.ptsFloor.forEach((pt, i) => {
      vec3.transformMat4(vec3temp, pt, this.camera._matrix);
      const x = vec3temp[0];
      const y = vec3temp[1];
      const z = vec3temp[2];

      const xC = this.camera.position[0];
      const yC = this.camera.position[1];
      const zC = this.camera.position[2];

      const f = z - zC;

      const x2 = (x - xC) * (f / z) + xC;
      const y2 = (y - yC) * (f / z) + yC;

      const currPoint = ptsFloor[i];
      currPoint.x = x2;
      currPoint.y = y2;
    });
    this.floorPlane.update();

    this.background.resize(app.screen.width, app.screen.height * 0.8);
    this.background.view.position.x =
      app.screen.width / 2 - app.screen.width / 2;
    this.background.view.position.y =
      this.tvPlane.position.y +
      this.tvPlane.points[3].y -
      app.screen.height * 0.8;
  }

  onResize(w, h) {
    const { width, height } = app.screen;

    this.g.position.x = w / 2;
    this.g.position.y = h / 2;

    this.tvPlane.position.x = w / 2;
    this.tvPlane.position.y = h / 2;
    this.floorPlane.position.x = w / 2;
    this.floorPlane.position.y = h / 2;

    const scale = Math.min(1, (width * 0.45) / 300);
    this.containerScene.position.x = width / 2;
    this.containerScene.position.y = height / 2;

    this.containerScene.pivot.x = width / 2;
    this.containerScene.pivot.y = height / 2;

    this.containerScene.scale.set(scale);
  }
}



