import {
  Loader,
  SCALE_MODES,
  Spritesheet,
  TYPES,
  WRAP_MODES,
  utils,
} from "pixi.js";

import WebFont from "webfontloader";
import allSettled from "promise.allsettled";
import { renderer } from "../WebGLAnimation";

export default class LoaderPixi {
  constructor({ optimize }) {
    this.optimize = optimize;
    this.loader = new Loader();

    this.resolveLoaderComplete = null;
    this.onLoaderComplete = new Promise(
      (resolve) => (this.resolveLoaderComplete = resolve)
    );

    this.fonts = [];
    this.customFonts = null;
    this.jsons = [];
    this.jsonMap = new Map();
    this.spritesheets = [];

    this.callback = null;
  }

  addManifest(manifests) {
    manifests.forEach((file) => {
      const isSpritesheet = file.src.includes("json");
      if (isSpritesheet) {
        let imageName = file.src.replace(".json", ".png");
        this.jsons.push(file.src);
        this.loader.add(imageName, imageName);
        this.spritesheets.push(imageName);
      } else {
        this.loader.add(file.src, file.src);
      }
    });
  }

  addOthers(others) {
    others.map((asset) => {
      console.log(asset.id || asset.src, asset.src);
      this.loader.add(asset.id || asset.src, asset.src);
    });
  }

  addFonts(fonts) {
    this.fonts.push(...fonts);
  }

  addCustomFonts(customFonts) {
    this.customFonts = customFonts;
  }
  /**
   * COPY from the former font loader
   */
  loadFonts() {
    return new Promise((resolve) => {
      let fontLoadingPromises = [];
      let fontLoadingHandlers = {};

      const onAllLoading = async () => {
        // don't mind if loaded or not, just that they've finished trying
        allSettled(fontLoadingPromises).then(() => {
          resolve();
        });
      };

      const config = {
        // when all fonts have been requested
        loading: () => {
          // only start a watcher on the Promises when they're all set
          onAllLoading();
        },
        // when each individual font is loading, add Promise to array
        fontloading: (familyName) => {
          fontLoadingPromises.push(
            new Promise((resolve, reject) => {
              fontLoadingHandlers[familyName] = { resolve, reject };
            })
          );
        },
        fontactive: (familyName) => {
          fontLoadingHandlers[familyName].resolve();
        },
        fontinactive: (familyName) => {
          console.error(`font ${familyName} failed to load`);
          fontLoadingHandlers[familyName].reject();
        },
      };

      if (this.fonts.length) {
        config.google = {
          families: this.fonts,
        };
      }
      if (this.customFonts) {
        config.custom = this.customFonts;
      }

      WebFont.load(config);
    });
  }

  load(cb) {
    this.callback = cb;

    // load the fonts, then the loader
    this.loadFonts().then(() => {
      this.loader.load(this.onComplete.bind(this));
    });
  }

  async onComplete(loader, r) {
    // now we're going to optimise the textures and create the pixi.js spritesheets
    await this.loadJsons();
    this.resizeTextures(r);
    this.createSpritesheets(r);
    this.resolveLoaderComplete();
  }

  async loadJsons() {
    for (let i = 0; i < this.jsons.length; i++) {
      const url = this.jsons[i];
      let imageName = url.replace(".json", ".png");
      const response = await fetch(url);
      const json = await response.json();

      this.jsonMap.set(imageName, json);
    }
  }

  /**
   * Resize textures and upload them on the GPU + optimisation if needed
   * @param {object} textures an array of PIXI.Texture
   */
  resizeTextures(textures) {
    for (const i in textures) {
      const texture = textures[i];

      let id = i;

      if (texture.tags && texture.tags.tps) {
        id = `${id}_image`;
      }

      const baseTexture = utils.BaseTextureCache[id];

      if (baseTexture) {
        if (texture.tags) {
          if (texture.tags.REPEAT) {
            baseTexture.wrapMode = WRAP_MODES.REPEAT;
          }

          if (texture.tags.NEAREST) {
            baseTexture.scaleMode = SCALE_MODES.NEAREST;
          }
        }

        if (this.optimize) {
          // OPTIMISE IF NEEDED HERE
          if (!texture.tags || (texture.tags && !texture.tags.high)) {
            baseTexture.type = TYPES.UNSIGNED_SHORT_4_4_4_4;
          }
        }

        // upload to the GPU
        renderer.texture.bind(baseTexture);
      }
    }
  }

  createSpritesheets() {
    let promises = [];

    this.spritesheets.forEach((imageName) => {
      promises.push(
        new Promise((resolve) => {
          const sprites = new Spritesheet(
            utils.TextureCache[imageName].baseTexture,
            this.jsonMap.get(imageName)
          );
          sprites.parse(() => {
            resolve();
          });
        })
      );
    });

    Promise.all(promises)
      .then((results) => {
        // When everything is loaded!
        this.callback && this.callback();
      })
      .catch((e) => {
        console.log("AssetsLoader", e.message, e);
      });
  }
}



