import { Application } from "pixi.js";
import Config from "./Config";
import Emitter from "events";
import Loader from "./helpers/Loader";
import Scene from "./Scene";
import assets from "./asset-list";
import objectAssign from "object-assign";

export let app = null;
export let renderer = null;
class WebGLAnimation extends Emitter {
  constructor(mConfig = {}) {
    super();

    this.isSupported = true;
    this._isPlaying = true;
    objectAssign(Config, mConfig);

    if (this.isSupported) {
      this.app = new Application({
        width: window.innerWidth,
        height: window.innerHeight,
        backgroundColor: 0x000000,
        autoDensity: true,
        antialias: true,
        resolution: Math.max(window.devicePixelRatio || 1, 1.5),
      });

      app = this.app;
      renderer = this.app.renderer;

      if (mConfig.container) {
        mConfig.container.appendChild(this.app.view);
        this.app.resizeTo = mConfig.container;
      } else {
        this.app.resizeTo = window;
      }

      this.stage = this.app.stage;
      this.preloader = new Loader({ optimize: false });

      this._loadAssets(mConfig.ASSETS_PATH || "assets/");
    } else {
      setTimeout(() => {
        this.emit("onError", {
          error: "Mobile not supported for this experiment",
        });
      }, 10);
    }
  }

  _loadAssets(assetsPath) {
    if (!this.isSupported) return;

    const images = assets.images.map((oAsset) => {
      oAsset.src = oAsset.src.replace("assets/", assetsPath);
      return oAsset;
    });

    this.preloader.addManifest(images || []); // array of images
    assets.fonts && this.preloader.addFonts(assets.fonts || []);
    assets.customFonts && this.preloader.addCustomFonts(assets.customFonts);
    assets.others && this.preloader.addOthers(current.assets.others);
    this.preloader.load();
    this.preloader.loader.onProgress.add((l, p) => {
      this.emit("onProgress", { percent: p });
    });
    this.preloader.onLoaderComplete.then((l, p) => {
      this._onAssetsLoaded(l, p);
    });
  }

  _onAssetsLoaded(l, p) {
    if (!this.isSupported) return;

    this.emit("onLoaded", { l, p });
    this._initScenes();
  }

  onToggleSound() {}

  start() {
    if (!this.isSupported) return;

    this.app.ticker.add(this._update.bind(this));
  }

  play() {
    if (!this.isSupported) return;

    this._isPlaying = true;
  }

  pause() {
    if (!this.isSupported) return;

    this._isPlaying = false;
  }

  _initScenes() {
    if (!this.isSupported) return;

    setTimeout(() => {
      this.view = new Scene();
      this.stage.addChild(this.view.view);
      this.view.resize(this.w, this.h);
      this.view.show();
    }, 100);
  }

  _update() {
    if (!this.isSupported) return;

    this.view && this.view.update();
  }

  resize(w, h) {
    if (!this.isSupported) return;

    this.app.resize();
    this.view && this.view.resize(w, h);
  }
}

export default WebGLAnimation;



