export default `
varying vec2 vTextureCoord;
uniform sampler2D uTexture;


void main(void)
{

  vec2 uv = vTextureCoord;
  vec3 texColor = texture2D(uTexture, uv).rgb;

  float alpha = smoothstep(0.3, 0.95, uv.y);
  
  gl_FragColor = vec4(texColor, alpha);  
  gl_FragColor *= alpha;  
}`;



