import { Graphics } from "pixi.js";

export default class PointPlane {
  constructor() {
    this.view = new Graphics();
    this.view.beginFill(0xff0000).drawCircle(0, 0, 20);

    this.view.interactive = true;
    this.view.buttonMode = true;
    this.view.mousedown = this.onDown.bind(this);
    this.view.mouseup = this.onUp.bind(this);

    this.down = false;

    this._x = 0;
    this._y = 0;
    this.pt = null;

    this.down = false;
  }

  get x() {
    return this._x;
  }
  set x(value) {
    this._x = value;
    this.view.position.x = this._x;
  }
  get y() {
    return this._y;
  }
  set y(value) {
    this._y = value;
    this.view.position.y = this._y;
  }

  onDown() {
    this.down = true;
  }
  onUp() {
    this.down = false;
  }
}



