import { Mesh, PlaneGeometry, Shader, Texture } from "pixi.js";

import frag from "./frag";
import vert from "./vert";

export default class Background {
  constructor() {
    const geometry = new PlaneGeometry(100, 100, 2, 2);

    this.tick = 0;
    const shader = Shader.from(vert, frag, {
      uTime: this.tick,
      uNoiseScale: 1.3,
      uBrightness: 0.9,
      uAlpha: 0.25,
      uColor: [1, 1, 1],
    });

    this.view = new Mesh(geometry, shader);

    this.geometry = geometry;
    this.shader = shader;
  }

  set color(value) {
    this.shader.uniforms.uColor = value;
  }

  update() {
    this.tick++;
    this.shader.uniforms.uTime = this.tick;
  }

  resize(w, h) {
    this.geometry.width = w;
    this.geometry.height = h;
    this.geometry.build();
  }
}



