import { Container } from "pixi.js";
import { app } from "../WebGLAnimation";

export default class AbstractScene {
  constructor(config = {}, thumbnailAt) {
    this.view = new Container();
  }

  /**
   * The following methods are not to be overrided
   * they will call their equivalent that the child class can override
   * eg: `reset` -> `onReset`,  `load` -> `onLoad`, etc.
   */

  resize(w, h) {
    this.w = app.screen.width;
    this.h = app.screen.height;

    this.isPortrait = this.w < this.h;
    this.onResize(this.w, this.h);
  }

  update() {}

  pause() {
    this.paused = true;
    this.onPause();
  }

  resume() {
    this.paused = false;
    this.onResume();
  }

  show() {
    this.onShow();
  }

  hide() {
    this.onHide();
  }

  /**
   * Called when everything is loaded / resized
   * Good place to add events, create timeline etc.
   */
  onShow() {}

  /**
   * Called when the slideshow is paused
   * By default, pause the timeline since every slide are timeline based for now
   */
  onPause() {}

  /**
   * Called when the slideshow is resumes
   * By default, resume the timeline since every slide are timeline based for now
   */
  onResume() {}

  onResize(w, h) {}

  /**
   * Called when the slide is going to disappear
   * Good place to remove events, destroy stuff, etc.
   */
  onHide() {}
}



