import { Mesh, PlaneGeometry, Shader, Texture } from "pixi.js";

import frag from "./frag";
import vert from "./vert";

export default class Reflection {
  constructor(texture = Texture.WHITE) {
    const geometry = new PlaneGeometry(texture.width, texture.height, 2, 2);

    const shader = Shader.from(vert, frag, {
      uTexture: texture,
    });

    this.view = new Mesh(geometry, shader);

    this.geometry = geometry;
    this.shader = shader;

    this.texture = texture;
  }

  set texture(value) {
    this.shader.uniforms.uTexture = value;
    this.resize(value.width, value.height);
  }

  resize(w, h) {
    this.geometry.width = w;
    this.geometry.height = h;
    this.geometry.build();
  }
}



